import {
  EnumAction,
  EnumProfileType,
  EnumTarget,
  IAction,
  IProfileParam,
  ITarget,
} from "@/gateway/step";
import useDeviceContext from "@/hooks/useDeviceContext";
import { Button, Modal, Tiles } from "@buildo/bento-design-system";
import { produce } from "immer";
import { useTranslation } from "next-export-i18n";
import { useEffect, useState } from "react";
import { FaChartArea } from "react-icons/fa";
import { FaClock } from "react-icons/fa6";
import { GiManualMeatGrinder } from "react-icons/gi";
import { IoWaterSharp } from "react-icons/io5";
import { LiaFillDripSolid } from "react-icons/lia";

export const ActionRune = ({ index }: { index: number }) => {
  const [isOpen, setIsOpen] = useState(false);
  const { formulas, setFormulas, selectedFormulaId } = useDeviceContext();
  const [action, setAction] = useState<EnumAction>(
    formulas[selectedFormulaId].steps[index].action.actionType as EnumAction,
  );
  const step = formulas[selectedFormulaId].steps[index];
  const { t } = useTranslation();

  function handleChangeAction(e: EnumAction) {
    setFormulas(
      produce(formulas, (draft) => {
        const action = draft[selectedFormulaId].steps[index].action as IAction;
        action.actionType = e as EnumAction;
        const target = draft[selectedFormulaId].steps[index].target as ITarget;

        // set the target
        if (e == EnumAction.Grind) {
          target.targetType = EnumTarget.Weight;
        } else if (e == EnumAction.Extract) {
          target.targetType = EnumTarget.Weight;
        } else if (e == EnumAction.Rest) {
          target.targetType = EnumTarget.Time;
        } else if (e == EnumAction.Profile) {
          target.targetType = EnumTarget.Weight;
          let _action = action as IProfileParam;
          _action.segments = _action.segments || [];
          _action.profilingType =
            _action.profilingType || EnumProfileType.Flowrate;
        } else if (e == EnumAction.PourOver) {
          target.targetType = EnumTarget.Weight;
        }

        draft[selectedFormulaId].steps[index].action = action;
        draft[selectedFormulaId].steps[index].target = target;
      }),
    );
  }

  useEffect(() => {
    setIsOpen(false);
  }, [formulas]);

  return (
    <>
      <h1
        className="text-xl font-bold text-orange-900 underline decoration-pink-500"
        onClick={() => {
          setIsOpen(true);
        }}
      >
        {/* {step.action.actionType || "Action"} */}
        {t(step.action.actionType) || "Action"}
      </h1>
      {isOpen && (
        <Modal
          size="large"
          title={t("Select an action")}
          onClose={() => setIsOpen(false)}
          primaryAction={{
            label: t("OK"),
            onPress: () => {
              setIsOpen(false);
              handleChangeAction(action);
            },
          }}
          secondaryAction={{
            label: t("Cancel"),
            onPress: () => setIsOpen(false),
          }}
        >
          <Tiles space={4} columns={3}>
            <Button
              hierarchy="primary"
              kind={action == EnumAction.Grind ? "solid" : "outline"}
              label={t("Grind")}
              onPress={() => {
                setAction(EnumAction.Grind);
              }}
              icon={() => <GiManualMeatGrinder />}
              isDisabled={true}
            />
            <Button
              hierarchy="primary"
              kind={action == EnumAction.Extract ? "solid" : "outline"}
              label={t("Extract")}
              onPress={() => {
                setAction(EnumAction.Extract);
              }}
              icon={() => <IoWaterSharp />}
            />
            <Button
              hierarchy="primary"
              kind={action == EnumAction.Rest ? "solid" : "outline"}
              label={t("Rest")}
              onPress={() => {
                setAction(EnumAction.Rest);
              }}
              icon={() => <FaClock />}
            />
            <Button
              hierarchy="primary"
              kind={action == EnumAction.Profile ? "solid" : "outline"}
              label={t("Profile")}
              onPress={() => {
                setAction(EnumAction.Profile);
              }}
              icon={() => <FaChartArea />}
            />
            {/* <Button
              hierarchy="primary"
              kind={action == EnumAction.PourOver ? "solid" : "outline"}
              label={t("Pour Over")}
              onPress={() => {
                setAction(EnumAction.PourOver);
              }}
              icon={() => <LiaFillDripSolid />}
              isDisabled={true}
            /> */}
            {/* <Button
              hierarchy="primary"
              kind={action == EnumAction.Sound ? "solid" : "outline"}
              label={t("Sound")}
              onPress={() => {
                setAction(EnumAction.Sound);
              }}
              icon={() => <AiFillSound />}
            />
            <Button
              hierarchy="primary"
              kind={action == EnumAction.RGB ? "solid" : "outline"}
              label={t("RGB")}
              onPress={() => {
                setAction(EnumAction.RGB);
              }}
              icon={() => <TbColorFilter />}
            /> */}
          </Tiles>
        </Modal>
      )}
    </>
  );
};
