import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import { IProfileParam } from "@/gateway/step";
import useDeviceContext from "@/hooks/useDeviceContext";
import { SliderField, Columns, Column, Inline, NumberField, Banner, Modal, Stack } from "@buildo/bento-design-system";
import { produce } from "immer";
import { useTranslation } from "next-export-i18n";
import { useEffect, useRef, useState } from "react";
import { IoIosCloseCircle } from "react-icons/io";
import { Step } from '../../../gateway/step';

export function TimeRune({
  // value,
  //   onChange,
  // progress
  index,
  subIndex = -1,
}: {
  // value: number;
  //       onChange: Dispatch<SetStateAction<number>>;
  //      progress:  number;
  index: number;
  subIndex: number;
}) {
  const [isOpen, setIsOpen] = useState(false);
  const [current, setCurrent] = useState<number>(-1);
  const { formulas, setFormulas, selectedFormulaId } = useDeviceContext();
  const { formulaFeedbacks } = useDeviceContext();
  const [active, setActive] = useState(false);

  const step = formulas[selectedFormulaId].steps[index];

  const [time, setTime] = useState<number>(
    subIndex == -1
      ? step.target.value
      : (step.action as IProfileParam).segments[subIndex]?.duration || 0,
  );

  /*
    Progress doesn't always show up, it will disappear after 5 seconds
    So after 5 seconds, it must be clear to null
    So the number has its own internal state
  */

  let timeout = useRef<ReturnType<typeof setTimeout>>();


  useEffect(() => {
    const updated = subIndex == -1
      ? step.target.value
      : (step.action as IProfileParam).segments[subIndex].duration || 0;

    if (updated != current) {
      setTime(updated);
      setActive(true);

      clearTimeout(timeout.current);
      timeout.current = setTimeout(() => {
        setActive(false);
      }, 1000);
    }
  }, [formulas, selectedFormulaId, index, subIndex, step, current]);

  useEffect(() => {
    const updated = formulaFeedbacks[selectedFormulaId]?.[index]?.target?.value;
    // if you have subIndex, there nis no need to update progress, handled by the line chart already, more visual
    if (subIndex == -1) {
      setCurrent(updated || -1);
    }
  }, [formulaFeedbacks, index, selectedFormulaId, subIndex]);
  const { t } = useTranslation();

  function handleChangeAction() {
    setFormulas(
      produce(formulas, (draft) => {
        if (subIndex == -1) {
          draft[selectedFormulaId].steps[index].target.value = time;
        } else {
          const action = step.action as IProfileParam;
          action.segments[subIndex].duration = time;
        }
      }),
    );
  }

  return (
    <>
      {current != null && current > 0&& (
        <h1
          className=" text-xl font-bold text-green-800"
          onClick={() => setIsOpen(true)}
        >
          {`${(current / 1000).toFixed(1)}/`}
        </h1>
      )}
      
      <h1
        className=" text-xl font-bold text-blue-800"
        onClick={() => setIsOpen(true)}
      >
        {time.toFixed(1)}
        
        {" " + (time <= 1 ? t("second") : t("seconds"))}
      </h1>
      {isOpen && (
        <Modal
          size="small"
          title={t("How long?")}
          onClose={() => setIsOpen(false)}
          primaryAction={{
            label: t("OK"),
            onPress: () => {
              setIsOpen(false);
              handleChangeAction();
            },
          }}
          secondaryAction={{
            label: t("Cancel"),
            onPress: () => setIsOpen(false),
          }}
        >
          <Stack space={8}>
            <Columns space={4} align="center">
              <Column width={"4/5"}>
                <SliderField
                  type="single"
                  name="time"
                  value={time}
                  onChange={setTime}
                  label={t('Time')}
                  minValue={0}
                  maxValue={30}
                  step={1}
                />
              </Column>
              <Column width={"1/5"}>
                <NumberField
                  value={time}
                  onChange={setTime}
                  label={t('Time')}
                  minValue={0}
                  maxValue={500}
                  step={1}
                />
              </Column>
            </Columns>
          </Stack>
        </Modal>
      )}
    </>
  );


  return (
    <Popover
      onOpenChange={(e) => {
        setIsOpen(e != false);
      }}
      open={isOpen}
    >
      <PopoverTrigger>
        <div
          className="flex h-8 w-fit items-center justify-between rounded-lg p-2"
          onClick={() => {
            setIsOpen(true);
          }}
        >
          {current != -1 && (
            <h1
              className=" text-xl font-bold text-green-800"
              onClick={() => setIsOpen(true)}
            >
              {`${(current / 1000).toFixed(1)}/`}
            </h1>
          )}

          <h1 className=" text-xl font-bold text-blue-800">
            {time} {time <= 1 ? t("second") : t("seconds")}
          </h1>
        </div>
      </PopoverTrigger>
      <PopoverContent className="w-80 space-y-8 rounded-xl  border">
        <IoIosCloseCircle
          className="absolute right-2 top-2 text-gray-500"
          size={20}
          onClick={() => {
            setIsOpen(false);
          }}
        />

        <SliderField
          value={time}
          onChange={(e: number) => {
            setTime(e);
            setFormulas(
              produce(formulas, (draft) => {
                // draft[selectedFormulaId].steps[index].target.value = e;
                if (subIndex == -1) {
                  draft[selectedFormulaId].steps[index].target.value = e;
                } else {
                  const action = step.action as IProfileParam;
                  action.segments[subIndex].duration = e;
                }
              }),
            );
          }}
          label={t("Time")}
          type="single"
          name="value"
          minValue={0}
          maxValue={60}
          step={1}
        />
      </PopoverContent>
    </Popover>
  );
}
