import { IGrinderState } from "@/gateway/grinder";
import useDeviceContext from "@/hooks/useDeviceContext";
import {
  Banner,
  Button,
  Inline,
  Modal,
  Stack,
} from "@buildo/bento-design-system";
import { useTranslation } from "next-export-i18n";
import { useRouter } from "next/router";
import { Dispatch, SetStateAction, useState } from "react";
import { useEffectOnce } from "usehooks-ts";

export const IngredientRune = ({
  value,
  onChange,
}: {
  value: string;
  onChange: Dispatch<SetStateAction<string>>;
}) => {
  console.log("action/ recreate");

  const [isOpen, setIsOpen] = useState(false);
  const { grinderSettings } = useDeviceContext();

  const [ingredient, setIngridient] = useState<string>(value || "");
  const { t } = useTranslation();
  const router = useRouter();
  useEffectOnce(() => {
    onChange("");
  });

  return (
    <>
      <h1
        className=" text-xl font-bold text-blue-800"
        onClick={() => setIsOpen(true)}
      >
        {ingredient == "" ? t("[Select ingredient]") : ingredient}
      </h1>
      {isOpen && (
        <Modal
          size="small"
          title={t("Select ingredient")}
          onClose={() => setIsOpen(false)}
          primaryAction={{
            label: "OK",
            onPress: () => {
              setIsOpen(false);
              onChange(ingredient);
            },
          }}
          secondaryAction={{
            label: "Cancel",
            onPress: () => setIsOpen(false),
          }}
        >
          <Stack space={8}>
            <Inline space={4}>
              {grinderSettings.map((grinder: IGrinderState, index: number) => {
                return (
                  <Button
                    key={index}
                    kind={ingredient == grinder.name ? "solid" : "outline"}
                    hierarchy="primary"
                    label={grinder.name}
                    onPress={() => setIngridient(grinder.name)}
                  />
                );
              })}
              {/* <Button
                key={-1}
                kind={"outline"}
                hierarchy="primary"
                label={"+"}
                onPress={() => {
                  router.push("/setting");
                }}
              /> */}
            </Inline>
            <Banner
              kind="informative"
              title={t("Reminder")}
              description={t(
                "You can set up your grinder in the settings, the hardware can be connected to the machine wirelessly to the espresso machine",
              )}
              // action={{
              //   label: "Learn more",
              //   onPress: () => {
              //     window.open("https://www.google.com");
              //   },
              // }}
            />
          </Stack>
        </Modal>
      )}
    </>
  );
};
