"use client";


import { NewStepCard } from "@/components/step/StepCard";
import { Separator } from "@/components/ui/separator";
import { EnumAction, EnumTarget } from "@/gateway/step";
import useDeviceContext from "@/hooks/useDeviceContext";
import {
  Body,
  Button,
  Modal,
  TextField,
  Banner,
  Tiles,
  SliderField,
} from "@buildo/bento-design-system";
import { produce } from "immer";
import { useSelectedLanguage, useTranslation } from "next-export-i18n";
import { useRouter } from "next/router";
import { ReactNode, useEffect, useRef, useState } from "react";
import {
  DragDropContext,
  Draggable,  
  DropResult,
  Droppable,
  ResponderProvided,
} from "react-beautiful-dnd";
import { AiFillPlusCircle } from "react-icons/ai";
import {
  FaDownload,
  FaPlay,
  FaShareAlt,
  FaStop,
  FaWindowClose,
} from "react-icons/fa";
import { uuid } from "uuidv4";
import Layout from "./layout";
import { downloadFormula } from "@/gateway/formula";
import { queryDevice } from "@/gateway/connection";

const BetterButton = ({
  icon,
  label,
  color,
  onClick,
}: {
  icon: ReactNode;
  label: string;
  color: string;
  onClick: () => void;
}) => {
  return (
    <div
      className={`flex size-12 cursor-pointer flex-row items-center space-x-1 rounded-xl border bg-white p-4 hover:border-blue-500`}
    >
      {icon}
    </div>
  );
};

const Detail = () => {
  const router = useRouter();
  const id = router.query.slug;



  const [downloadTargetGroup, setDownloadTargetGroup] = useState(-1);
  const [downloadTargetButton, setDownloadTargetButton] = useState(-1);

  const [downloadWindow, setDownloadWindow] = useState(false);
  const [shareWindow, setShareWindow] = useState(false);
  const [loading, setLoading] = useState(true);

  const [running, setRunning] = useState<boolean>(false);
  const [shareToken, setShareToken] = useState<string>('')
  const {
    selectedFormulaId,
    registerSelectedFormulaId,
    setFormulas,
    formulas,
    setSelectedPage,  
    registerFormulaClick,
    cancelFormulaClick,
    progressReport,
  } = useDeviceContext();

  const [buildupDelay, setBuildupDelay] = useState(formulas[selectedFormulaId]?.buildupDuration || 0)

  const [winReady, setwinReady] = useState(false);
  const timeoutRef = useRef<ReturnType<typeof setTimeout>>();

  const { formulaFeedbacks } = useDeviceContext();

  useEffect(() => {
    if (timeoutRef) {
      clearTimeout(timeoutRef.current);
      timeoutRef.current = setTimeout(() => {
        setRunning(false);
      }, 2000);
      setRunning(true);
    }
  }, [formulaFeedbacks, selectedFormulaId]);

  useEffect(() => {
    setTimeout(async () => {
      await queryDevice({
        command: 'group.locate',
        buttonId: downloadTargetButton + 1,
        groupId: downloadTargetGroup + 1,
      })

    })
  }, [downloadTargetGroup, downloadTargetButton])

  //TODO: Assume don't have any formular before
  function handleAddStep() {
    // setSteps(
    //   produce(steps, (draft) => {
    //     draft.push({
    //       id: uuid(),
    //       action: {
    //         actionType: EnumAction.Rest,
    //         valveState: false,
    //       },
    //       target: {
    //         targetType: EnumTarget.Time,
    //         value: 10,
    //       },
    //     });
    //   })
    // );

    setFormulas(
      produce(formulas, (draft) => {
        const formula = draft[selectedFormulaId];
        formula.steps.push({
          id: uuid(),
          action: {
            actionType: EnumAction.Rest,
            valveState: false,
          },
          target: {
            targetType: EnumTarget.Time,
            value: 10,
          },
        });
      }),
    );
  }

  function handleDeleteStep(id: string) {
    // setSteps(
    //   produce(steps, (draft) => {
    //     return draft.filter(function (item) {
    //       return item?.id !== id;
    //     });
    //   })
    // );

    setFormulas(
      produce(formulas, (draft) => {
        const formula = draft[selectedFormulaId];
        formula.steps = formula.steps.filter(function (item) {
          return item?.id !== id;
        });
        return draft;
      }),
    );
  }

  function handleDragAndDrop(result: DropResult, provided: ResponderProvided) {
    if (!result.destination) return;
    // setSteps(
    //   produce(steps, (draft) => {
    //     const [reorderedItem] = draft.splice(result.source.index, 1);
    //     draft.splice(result.destination.index, 0, reorderedItem);
    //     return draft;
    //   })
    // );

    setFormulas(
      produce(formulas, (draft) => {
        const formula = draft[selectedFormulaId];
        const [reorderedItem] = formula.steps.splice(result.source.index, 1);
        formula.steps.splice(result?.destination?.index || 0, 0, reorderedItem);
        return draft;
      }),
    );
  }

  useEffect(function () {
    setwinReady(true);
  }, []);
  useEffect(() => {
    console.log("lab/ re-render", selectedFormulaId);
  }, [selectedFormulaId]);

  const listDrinkType = () => {
    return (
      <div className="flex h-16 w-full flex-row justify-between space-x-4"></div>
    );
  };
  const { lang } = useSelectedLanguage();
  const { t } = useTranslation();
  const translations_run = t("Run");
  return (
    <Layout>
      {downloadWindow && (
        <Modal
          size="large"
          autoFocus={false}
          title={t("Assign to Button")}
          onClose={() => setDownloadWindow(false)}
          primaryAction={{
            label: t("Download"),
            onPress: async () => {
              console.log('lab/ download formula to', { downloadTargetGroup, downloadTargetButton, selectedFormulaId })

              await downloadFormula({ groupId: downloadTargetGroup + 1, buttonId: downloadTargetButton + 1, formulaId: selectedFormulaId })

              setLoading(true);
              setTimeout(() => {
                setDownloadWindow(false);
              }, 1000);
            },
          }}
          secondaryAction={{
            label: t("Cancel"),
            onPress: () => setDownloadWindow(false),
          }}
        >
          <Body size="medium">
            {t(
              "This process will save the formula to a physical button on your cofee machine so you don't always need a mobile device connected",
            )}
            <br />
            <br />

            <Banner
              kind="informative"
              title={t('Notice')}
              description={t(
                "The button order might not be the same physically, right after clicking saved, the button will blink to indicate which button it is in real life"
              )}
              action={{
                label: "Learn more",
                onPress: () => {
                  window.open("https://www.google.com");
                },
              }}
            />


          </Body>

          <div className="flex h-32 w-full flex-col space-y-4 p-4">
            <Tiles space={8} columns={3}>
              {[1, 2, 3].map((value, index) => {
                return (
                  <Button
                    key={index}
                    label={t(`Group ${value}`)}
                    hierarchy="primary"
                    kind={index === downloadTargetGroup ? "solid" : "outline"}
                    onPress={() => setDownloadTargetGroup(index)}
                  />
                );
              })}
            </Tiles>
            <Tiles space={8} columns={4}>
              {[1, 2, 3, 4].map((value, index) => {
                return (
                  <Button
                    key={index}
                    label={t(`Button ${value}`)}
                    hierarchy="primary"
                    kind={index === downloadTargetButton ? "solid" : "outline"}
                    onPress={() => setDownloadTargetButton(index)}
                  />
                );
              })}
            </Tiles>
          </div>
        </Modal>
      )}

      {shareWindow && (
        <Modal
          title="Share"
          onClose={() => setShareWindow(false)}
          primaryAction={{
            label: "OK",
            onPress: () => setShareWindow(false),
          }}
          secondaryAction={{
            label: "Cancel",
            onPress: () => setShareWindow(false),
          }}
        >
          <Body size="medium">{shareToken}</Body>
        </Modal>
      )}

      <div className="flex flex-col  items-center space-y-4 p-4">
        <div
          className={
            " flex  h-fit w-96 max-w-[calc(90vw)] flex-col space-y-4 rounded-xl border p-4 shadow-xl"
          }
        >
          <TextField
            value={formulas[selectedFormulaId]?.title}
            onChange={(e: string) => {
              setFormulas(
                produce(formulas, (draft) => {
                  console.log("Formulas", formulas, selectedFormulaId);
                  draft[selectedFormulaId].title = e;
                }),
              );
            }}
            label={t("Formula")}
            placeholder="Untitled formula"
          />

          {/* {listDrinkType()} */}

          <div className="flex flex-row items-center justify-between space-x-1 text-white">
            {running === true ? (
              <div
                className={`flex h-12 w-2/5 cursor-pointer select-none flex-row items-center justify-evenly rounded-xl border bg-white bg-gradient-to-r from-orange-400 to-red-400 p-4 text-white hover:border-blue-500`}
                onClick={() => {
                  cancelFormulaClick(selectedFormulaId);
                }}
              >
                <FaStop />
                <h1>Stop</h1>
              </div>
            ) : (
              <div
                className={`flex h-12 w-2/5 cursor-pointer select-none flex-row items-center justify-evenly rounded-xl border bg-white bg-gradient-to-r from-blue-400 to-emerald-400 p-4 text-white hover:border-blue-500`}
                onClick={() => {
                  // console.log("play");
                  registerFormulaClick(selectedFormulaId);
                }}
              >
                <FaPlay />
                <h1>{t("Run")}</h1>
              </div>
            )}
            <div
              className={`flex h-12 w-1/5 cursor-pointer flex-row items-center justify-evenly rounded-xl border bg-white bg-gradient-to-r from-purple-800 via-violet-900 to-purple-800 p-4 hover:border-blue-500`}
              onClick={() => {
                console.log("download");
                // open a bento model
                setDownloadWindow(true);
              }}
            >
              <FaDownload />
            </div>

            <div
              className={`flex h-12 w-1/5 cursor-pointer flex-row items-center justify-evenly rounded-xl border bg-white bg-gradient-to-r from-blue-500 to-blue-600 p-4 hover:border-blue-500`}
              onClick={async () => {

                const data = {
                  formulas: formulas[selectedFormulaId],
                  timestamp: Date.now()
                }
                const request = await fetch('https://api.anatalab.com/gateway/rosario/create_token', {
                  method: 'POST',
                  headers: {
                    'Content-Type': 'application/json'
                  },
                  body: JSON.stringify(data)
                })
                
                const response = await request.json()
                console.log('Share token response',response)

                const assignedToken = response.token
                setShareToken(assignedToken)
                setShareWindow(true);
              }}
            >
              <FaShareAlt />
            </div>

            <div
              className={`flex h-12 w-1/5 cursor-pointer flex-row items-center justify-evenly rounded-xl border bg-white bg-gradient-to-r from-red-500 to-red-800 p-4 hover:border-blue-500`}
              onClick={() => {
                console.log("delete");
                setSelectedPage("Formula");
                registerSelectedFormulaId("");
                setFormulas(
                  produce(formulas, (draft) => {
                    // return draft.filter(item => item?.id !== selectedFormulaId)
                    delete formulas[selectedFormulaId];
                  }),
                );

                router.push("/");
              }}
            >
              <FaWindowClose />
            </div>
          </div>
          
          <SliderField
            type="single"
            name="delay"
            label={t("Pressure build-up time")}
            value={formulas[selectedFormulaId]?.buildupDuration || 0}
            onChange={(e: number) => {
              setFormulas(
                produce(formulas, (draft) => {
                  console.log("Formulas", formulas, selectedFormulaId);
                  if (draft[selectedFormulaId]) {
                    draft[selectedFormulaId].buildupDuration = e;
                  }
                  return draft
                }),
              );
            }}
            minValue={0}
            maxValue={10}
          />


        </div>
        <Separator />
        <div className=" mix-w-80 flex flex-col items-center justify-center py-3 ">
          <DragDropContext
            onDragEnd={handleDragAndDrop}
            onBeforeDragStart={() => {
              console.log("onBeforeDragStart");
            }}
            onDragStart={() => {
              console.log("onDragStart");
            }}
            onDragUpdate={() => {
              console.log("onDragUpdate");
            }}
          >
            <div className="flex w-full flex-row">
              <Droppable droppableId="ROOT">
                {(provided) => (
                  <div
                    {...provided.droppableProps}
                    ref={provided.innerRef}
                    className="mx-auto flex w-full flex-col items-center justify-between space-y-4"
                  >
                    {(formulas[selectedFormulaId]?.steps || [])?.map(
                      (item, idx) => {
                        console.log("render", { item, idx });
                        return (
                          winReady && (
                            <Draggable
                              draggableId={item?.id}
                              key={item?.id}
                              index={idx}
                            >
                              {(provided) => (
                                <div
                                  className=""
                                  {...provided.dragHandleProps}
                                  {...provided.draggableProps}
                                  ref={provided.innerRef}
                                >
                                  <NewStepCard
                                    onDelete={() => handleDeleteStep(item?.id)}
                                    index={idx}
                                  // onChange={(params) =>
                                  //   handleChange(item?.id, params)
                                  // }
                                  />
                                </div>
                              )}
                            </Draggable>
                          )
                        );
                      },
                    )}
                    {provided.placeholder}
                  </div>
                )}
              </Droppable>
            </div>
          </DragDropContext>
          <button
            className="my-4 flex h-16 w-80 cursor-pointer flex-row items-center  justify-center space-x-4 rounded-xl border bg-blue-100"
            onClick={handleAddStep}
          >
            <AiFillPlusCircle color="#1180EF" size={30} />
            <p className="text-lg font-bold text-[#1180EF]">{t("Add Step")}</p>
          </button>
        </div>
      </div>
    </Layout>
  );
};

export default Detail;
