import { Banner, Modal, SliderField, Stack, NumberField, Columns, Column } from "@buildo/bento-design-system";
import { Dispatch, SetStateAction, useState } from "react";
import { useEffectOnce } from "usehooks-ts";
import { useTranslation } from "next-export-i18n";

export const PumpRune = ({
  value,
  onChange,
}: {
  value: number;
  onChange: Dispatch<SetStateAction<number>>;
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [power, setPower] = useState<number>(value == null ? 100 : value);

  useEffectOnce(() => {
    onChange(power)
  })
  const {t} = useTranslation()

  return (
    <>
      <div
        className="flex h-8 w-fit items-center justify-between rounded-lg p-2"
        onClick={() => {
          setIsOpen(true);
        }}
      >
        <h1 className="text-xl font-bold text-blue-800">{value === null ? 100 : value} %</h1>
      </div>

      {isOpen && (
        <Modal
          size="small"
          title={t("Set pump power")}
          onClose={() => setIsOpen(false)}
          primaryAction={{
            label: "OK",
            onPress: () => {
              setIsOpen(false);
              console.log(" update power", power);
              onChange(power);
            },
          }}
          secondaryAction={{
            label: "Cancel",
            onPress: () => setIsOpen(false),
          }}
        >
          <Stack space={8}>
            <Columns space={4} align="center">
              <Column width={"4/5"}>
                <SliderField
                  type="single"
                  name="power"
                  value={power}
                  onChange={(e: number) => {
                    console.log("Power is updated for pump", e);
                    setPower(e);
                  }}
                  label={t("Pump Power")}
                  minValue={0}
                  maxValue={100}
                  step={1}
                />
              </Column>
              <Column width={"1/5"}>
                <NumberField
                  value={power}
                  onChange={setPower}
                  onBlur={() => console.log("blur")}
                  label={t("")}
                  
                  minValue={0}
                  maxValue={100}
                  step={1}
                />
              </Column>
            </Columns>

            <Banner
              kind="informative"
              title={t("Notice")}
              description={t("Power require an external hardware to be connected to the machine, other wise it is simply ON/OFF")}
              action={{
                label: "Learn more",
                onPress: () => {
                  window.open("https://www.google.com");
                },
              }}
            />
          </Stack>
        </Modal>
      )}
    </>
  );
};
