type PressureDtps = {
  timestamp: number;
  min: number;
  max: number;
  pressure?: number;
  flowrate?: number;
};
type FlowrateDtps = {
  timestamp: number;
  // flowrate: number;
  min: number;
  max: number;
  pressure?: number;
  flowrate?: number;
};
type PowerDtps = {
  timestamp: number;
  power: number;
  min: number;
  max: number;
  pressure?: number;
  flowrate?: number;
};

export type DataPoint = PressureDtps | FlowrateDtps | PowerDtps;

const generateDatapoints = (
  segments: IProfilingSegment[],
  sensor: EnumProfileType,
  formulaFeedbacks?: IStepFeedback["action"][],
) => {
  if (segments === undefined) return [];

  const dps: DataPoint[] = [];
  // for each segments, we add a new point
  let totalTime = 0;
  segments.forEach((segment) => {
    totalTime += segment.duration;
  });

  var startTime = 0;
  var previousValue = 0;
  for (const segment of segments) {
    if (segment.smooth == false) {
      console.log("smooth is real")
      // add an additional step
      if (sensor == EnumProfileType.Pressure) {
        dps.push({
          timestamp: startTime,
pressure: segment.value,
          min: 0,
          max: 11,
        })
      }
      else if (sensor == EnumProfileType.Flowrate) {
        dps.push({
          timestamp: startTime,
          flowrate: segment.value,
          min: 0,
          max: 51,
        })
      }
      else if (sensor == EnumProfileType.Power) {
        dps.push({
          timestamp: startTime,
          power: segment.value,
          min: 0,
          max: 101,
        })
      }
      previousValue = segment.value
    }



    for (var time = 0; time < segment.duration; time += 1) {
      var interimValue =
        previousValue +
        (segment.value - previousValue) * (time / segment.duration);
      interimValue = Math.round(interimValue * 10) / 10;
      if (sensor == EnumProfileType.Pressure) {
        dps.push({
          timestamp: startTime + time,
pressure: interimValue,
          max: 10,
          min: 0,
          // time: timestamp
        });
      } else if (sensor == EnumProfileType.Flowrate) {
        dps.push({
          timestamp: startTime + time,
          flowrate: interimValue,
          max: 50,
          min: 0,
          // time: timestamp
        });
      } else if (sensor == EnumProfileType.Power) {
        dps.push({
          timestamp: startTime + time,
          power: interimValue,
          max: 100,
          min: 0,
          // time: timestamp
        });
      }
    }
    previousValue = segment.value;
    startTime += segment.duration;
  }
  return dps;
};
