export const SegmentModeRune = ({
  index,
  subIndex = -1,
}: {
  index: number;
  subIndex: number;
}) => {

  const { formulas, setFormulas, selectedFormulaId } = useDeviceContext()
  const step = formulas[selectedFormulaId].steps[index]
  const { t } = useTranslation()


  function handleSwap() {
    setFormulas(produce(formulas, (draft) => {
      (draft[selectedFormulaId].steps[index].action as IProfileParam).segments[subIndex].smooth = !(draft[selectedFormulaId].steps[index].action as IProfileParam).segments[subIndex].smooth
    }))

  }


  /*
    add reaching up or down
    it need to have access to value of previous segment only
  */
  var previousValue = 0
  if (subIndex > 0) {
    previousValue = (step.action as IProfileParam).segments[subIndex - 1].value
  }

  var isUpward = previousValue < (step.action as IProfileParam).segments[subIndex].value
  


  return <>
    <h1
      className="text-xl font-bold text-orange-900"
      onClick={handleSwap}
    >
      {/* {step.action.actionType || "Action"} */}
      {(step.action as IProfileParam).segments[subIndex].smooth ? (
        isUpward ? t("slowly reaching up") : t("slowly reaching down")
      ) : (t("hold at"))}
    </h1>
  </>
}

export const MagicLine = ({
  // segments,
  // sensor,
  index,
}: {
  index: number;
  // segments: IProfilingSegment[];
  // sensor: EnumProfileType;
}) => {
  const [dataPoints, setDataPoints] = useState<DataPoint[]>([]);
  const { formulas, setFormulas, selectedFormulaId } = useDeviceContext();
  const { formulaFeedbacks } = useDeviceContext();

  const step = formulas[selectedFormulaId].steps[index];
  const action = step.action as IProfileParam;
  const sensor = action.profilingType;
  const segments = action.segments;

  const fbstore = useRef<IStepFeedback["action"][]>([]);

  useEffect(() => {
    try {
      const actionUpdate = formulaFeedbacks[selectedFormulaId][index]?.action as IStepFeedback["action"];

      if (!actionUpdate) {
        console.warn("No update from profileRune")
        return;
      }
      // when ever formulaFeedbacks are updated, store the values here
      console.log("Line/ add value", actionUpdate);

      fbstore.current = (window as any).fbstore || [];
      (window as any).fbstore = fbstore.current;

      var store = fbstore.current;
      store.push({
        ...actionUpdate,
        timestamp: Math.round(actionUpdate.timestamp / 1000),
      });
      // filter out the store, using the timestamp, remove those that has higher timestmap than actionUpdate
      // store = store.filter((s) => s.timestamp >= Math.round(actionUpdate.timestamp / 1000))

      const dpts = generateDatapoints(segments, sensor, store);
      // add the current reporting in
      dpts.map((dpt, idx) => {
        var current = null;
        store.forEach((s) => {
          // find the latest value
          if (s.timestamp == dpt.timestamp) {
            dpt.flowrate = s.input;
            dpt.pressure = s.pressure * 10 || 0;
          }
        });
      });
      console.log("Line/ dpts", dpts, store);
      setDataPoints(dpts);
    } catch (e) {
      console.error("ProfileRune", e);
    }
  }, [formulaFeedbacks, index, segments, selectedFormulaId, sensor]);

  useEffect(() => {
    setDataPoints(generateDatapoints(segments, sensor));
  }, [segments, sensor]);

  const attrMap = {};
  attrMap[EnumProfileType.Pressure] = "pressure";
  attrMap[EnumProfileType.Flowrate] = "flowrate";
  attrMap[EnumProfileType.Power] = "power";

  return (
    <div className="hover:scale-120 w-full -translate-x-10 transition-transform duration-200">
      <LineChart
        key={index}
        height={300}
        dataKey="Time"
        categories={[
          // add the sensor of power herer

          // sensor == EnumProfileType.Pressure ? "pressure" : "flowrate",
          attrMap[sensor] || "unknown sensor",
          "flowrate",
          // "max",
          // "min",
          "pressure"
        ]}
        hideLegend={false}
        hideXAxis={true}
        hideYAxis={false}
        data={dataPoints}
        disableAnimation={true}
      />
    </div>
  );
};
const ProfileValueRune = ({
  // segments,
  // setValue,
  // sensor,
  // idx,
  index,
  subIndex,
}: {
  // segments: IProfilingSegment[];
  // setValue: Dispatch<SetStateAction<number>>;
  // sensor: EnumProfileType;
  // idx: number;
  index: number;
  subIndex: number;
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const { formulas, setFormulas, selectedFormulaId } = useDeviceContext();
  const step = formulas[selectedFormulaId].steps[index];
  const action = step.action as IProfileParam;
  const sensor = action.profilingType;
  const segments = action.segments;
  const idx = subIndex;

  let unit: string = "";
  if (sensor == EnumProfileType.Pressure) {
    unit = "bar";
  }
  if (sensor == EnumProfileType.Flowrate) {
    unit = "gram/s";
  } else if (sensor == EnumProfileType.Power) {
    unit = "%";
  }

  const maxValue: Record<EnumProfileType, number> = {
    [EnumProfileType.Pressure]: 10,
    [EnumProfileType.Flowrate]: 100,
    [EnumProfileType.Power]: 100,
  };
  const [value, setValue] = useState<number>(segments[subIndex].value)
  const { t } = useTranslation()
  
  
  function handleUpdate() {
    setFormulas(
      produce(formulas, (draft) => {
        (draft[selectedFormulaId].steps[index].action as IProfileParam).segments[subIndex].value = value
      })
    )
  }


  return (
    <>
      <div
        className="flex h-8 w-fit items-center justify-between rounded-lg p-2"
        onClick={() => {
          setIsOpen(true);
        }}
      >
        <h1 className="text-xl font-bold text-blue-800">{value === null ? 100 : value} %</h1>
      </div>

      {isOpen && (
        <Modal
          size="small"
          title={t("Set target")}
          onClose={() => setIsOpen(false)}
          primaryAction={{
            label: t("OK"),
            onPress: () => {
              setIsOpen(false);
              handleUpdate();
            },
          }}
          secondaryAction={{
            label: t("Cancel"),
            onPress: () => setIsOpen(false),
          }}
        >
          <Stack space={8}>
            <Columns space={4} align="center">
              <Column width={"4/5"}>
                <SliderField
                  type="single"
                  name="power"
                  value={value}
                  onChange={(e: number) => {
                    setValue(e);
                  }}
                  label={t(sensor)}
                  minValue={0}
                  maxValue={100}
                  step={1}
                />
              </Column>
              <Column width={"1/5"}>
                <NumberField
                  value={value}
                  onChange={setValue}
                  onBlur={() => console.log("blur")}
                  label={t(sensor)}

                  minValue={0}
                  maxValue={100}
                  step={1}
                />
              </Column>
            </Columns>

            {/* <Banner
              kind="informative"
              title={t("Notice")}
              description={t("Power require an external hardware to be connected to the machine, other wise it is simply ON/OFF")}
              action={{
                label: "Learn more",
                onPress: () => {
                  window.open("https://www.google.com");
                },
              }}
            /> */}
          </Stack>
        </Modal>
      )}
    </>
  );

  return (
    <Popover
      onOpenChange={(e) => {
        console.log("close", e);
        if (e == false) {
          setIsOpen(false);
        }
      }}
      open={isOpen}
    >
      <PopoverTrigger>
        <div
          className="flex h-8 w-fit items-center justify-between rounded-lg p-2"
          onClick={() => {
            setIsOpen(true);
          }}
        >
          <h1 className=" text-xl font-bold text-blue-800">
            {segments[idx].value} {unit}
          </h1>
        </div>
      </PopoverTrigger>
      <PopoverContent className="w-96 min-w-96 rounded-xl border">
        <IoIosCloseCircle
          className="absolute right-2 top-2 text-gray-500"
          size={20}
          onClick={() => {
            setIsOpen(false);
          }}
        />

        <SliderField
          value={segments[idx].value}
          onChange={(e: number) => {
            setFormulas(
              produce(formulas, (draft) => {
                (
                  draft[selectedFormulaId].steps[index].action as IProfileParam
                ).segments[idx].value = e;
              }),
            );
          }}
          label={sensor}
          type="single"
          name="value"
          minValue={0}
          maxValue={maxValue[sensor]}
          step={0.1}
        />
      </PopoverContent>
    </Popover>
  );
};

export const ProfileRune = ({
  // value,
  // onChange,
  index,
}: {
  // value: EnumProfileType;
  // onChange: Dispatch<SetStateAction<string>>;
  index: number;
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const { formulas, setFormulas, selectedFormulaId } = useDeviceContext();
  const step = formulas[selectedFormulaId].steps[index];
  const action = step.action as IProfileParam;

  const [profileType, setProfileType] = useState<EnumProfileType>(
    action.profilingType || EnumProfileType.Flowrate,
  );

  const { t } = useTranslation();
  useEffect(() => {
    setIsOpen(false);
  }, [formulas]);

  function onChange(sensor: EnumProfileType) {
    setFormulas(
      produce(formulas, (draft) => {
        draft[selectedFormulaId].steps[index].action.actionType =
          EnumAction.Profile;
        (
          draft[selectedFormulaId].steps[index].action as IProfileParam
        ).profilingType = sensor;
      }),
    );
  }

  return (
    <>
      <h1
        className=" text-xl font-bold text-blue-800"
        onClick={() => {
          setIsOpen(true);
        }}
      >
        {t(action.profilingType) || EnumProfileType.Flowrate}
      </h1>
      {isOpen && (
        <Modal
          size="small"
          title="Select an profiling method"
          onClose={() => setIsOpen(false)}
          primaryAction={{
            label: "OK",
            onPress: () => {
              setIsOpen(false);
              onChange(profileType);
            },
          }}
          secondaryAction={{
            label: "Cancel",
            onPress: () => setIsOpen(false),
          }}
        >
          <Stack space={16}>
            <Tiles space={8} columns={2}>
              <Button
                hierarchy="primary"
                kind={
                  profileType == EnumProfileType.Flowrate ? "solid" : "outline"
                }
                label="Flowrate"
                onPress={() => {
                  setProfileType(EnumProfileType.Flowrate);
                }}
              />

              <Button
                hierarchy="primary"
                kind={
                  profileType == EnumProfileType.Pressure ? "solid" : "outline"
                }
                label="Pressure"
                onPress={() => {
                  setProfileType(EnumProfileType.Pressure);
                }}
              />
              <Button
                hierarchy="primary"
                kind={
                  profileType == EnumProfileType.Power ? "solid" : "outline"
                }
                label="Power"
                onPress={() => {
                  setProfileType(EnumProfileType.Power);
                }}
              />
            </Tiles>
          </Stack>
        </Modal>
      )}
    </>
  );
};
