export const GrinderSkipRune = ({
  value,
  onChange,
}: {
  value: boolean;
  onChange: Dispatch<SetStateAction<boolean>>;
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const { t } = useTranslation();
  useEffect(() => {
    setIsOpen(false);
  }, [value]);
  useEffectOnce(() => {
    onChange(false);
  });

  const [skippable, setSkippable] = useState<boolean>(true);

  return (
    <>
      <h1
        className=" text-xl font-bold text-blue-800"
        onClick={() => setIsOpen(true)}
      >
        {skippable ? t("can skip") : t("must wait")}
      </h1>

      {isOpen && (
        <Modal
          autoFocus={false}
          size="small"
          title={t("Set grinder behaviour")}
          onClose={() => setIsOpen(false)}
          primaryAction={{
            label: "OK",
            onPress: () => {
              setIsOpen(false);
              onChange(skippable);
            },
          }}
          secondaryAction={{
            label: t("Cancel"),
            onPress: () => setIsOpen(false),
          }}
        >
          <Stack space={8}>
            <Tiles space={8} columns={2}>
              <Button
                kind={skippable ? "solid" : "outline"}
                hierarchy="primary"
                label={t("Can skip")}
                onPress={() => setSkippable(true)}
                icon={() => <IoMdSkipForward />}
              />
              <Button
                kind={!skippable ? "solid" : "outline"}
                hierarchy="primary"
                label={t("Must wait")}
                onPress={() => setSkippable(false)}
                icon={() => <FaRegClock />}
              />
            </Tiles>
            <Banner
              kind="informative"
              title={t("Explaination")}
              description={
                skippable
                  ? t(
                      "Grinder will skip this step if portafilter is not in position",
                    )
                  : t(
                      "Grinder will wait until the portafilter is in position, then it will start grinding",
                    )
              }
            />
          </Stack>
        </Modal>
      )}
    </>
  );
};
